import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import About from '../views/About.vue'
import Projects from '../views/Projects.vue'
import Teams from '../views/Teams.vue'
import Frontend from '../views/role/Frontend.vue'
import Backend from '../views/role/Backend.vue'
import Ui from '../views/role/Ui.vue'
import Designer from '../views/role/Designer.vue'
import Analisis from '../views/role/Analisis.vue'
import Kosong from '../views/404.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    component: About
  },
  {
    path: '/projects',
    name: 'Projects',
    component: Projects
  },
  {
    path: '/teams',
    name: 'Teams',
    component: Teams
  },
  {
    path: '/role/frontend',
    name: 'Frontend',
    component: Frontend
  },
  {
    path: '/role/backend',
    name: 'Backend',
    component: Backend
  },
  {
    path: '/role/ui',
    name: 'Ui',
    component: Ui
  },
  {
    path: '/role/designer',
    name: 'Designer',
    component: Designer
  },
  {
    path: '/role/analisis',
    name: 'Analisis',
    component: Analisis
  },
  {
    path: '/404',
    name: 'Kosong',
    component: Kosong
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
